




































export default {
  name:'goodsDetail',
  data(){
      return{
        noInfo:''
      }
  },
  computed:{
      goodsDetail(){
          return this.$store.state.shop.goodsDetail
      },
      goodsStatus(){
          return this.$store.state.shop.goodsStatus
      }
  },
  mounted(){
    this.$axios({
      method:'get',
      url: 'https://jiepl-show.github.io/web/data/data.json'
    }).then((response)=>{
      console.log(response)
      if(response.status==200){
        this.noInfo = response.data.data.others[2].imgurl3
      }
    }).catch((error)=>{
      console.log(error)
    })
  },
  methods:{
      changeStatus(){
          this.$store.commit("changeGoodsStatus")
      },
      addCart(val){
          this.$emit("cartHandle",val)
      },
      toShopCar(goodsDetail){
          this.changeStatus()
          this.$store.commit("toShopCar",goodsDetail)
          this.$router.push('/shopCar')
      }
  }  
}
