



































import search from '@/components/Search.vue'
import banner from '@/components/Banner.vue'
import goodsDetail from '@/components/GoodsDetail.vue'
import andCart from '@/components/AddCart.vue'
export default {
  name:'home',
  data() {
    return {
      imgList:[],
      typeList:[],
      goodsList:[],
      cartDetail:{}
    }
  },
  components:{
    search,banner,goodsDetail,andCart
  },
  created() {
    this.$axios({
      method:'get',
      url:'https://jiepl-show.github.io/web/data/data.json'
    }).then((response)=>{
      console.log(response)
      if(response.status==200){
        this.imgList = response.data.data.banner
        this.typeList = response.data.data.typeList
        this.goodsList = response.data.data.goodsList
      }
    }).catch((error)=>{
      console.log(error)
    })
  },
  methods: {
    goodsDetail(val){
      this.$store.dispatch("set_goodsDetail",val)
      this.$store.commit("changeGoodsStatus")
    },
    addCart(val){
      this.cartDetail=val
      this.$store.commit("changeCartStatus")
    },
    cartHandle(val){
      this.cartDetail=val
      this.$store.commit("changeCartStatus")
    }
  },
}
