






































export default {
name:'user',
  data(){
    return{
      user:'',
      orderIndex:null
    }
  },
  computed:{
    orderList(){
      return this.$store.state.shop.orderList
    }
  },
  mounted () {
    this.$axios({
      method:'get',
      url: 'https://jiepl-show.github.io/web/data/data.json'
    }).then((response)=>{
      console.log(response)
      if(response.status==200){
        this.user = response.data.data.others[0].imgurl1
      }
    }).catch((error)=>{
      console.log(error)
    })
  },
  methods:{
    toShop(){
      this.$router.push('/')
    },
    checkOrder(index){
      if(this.orderIndex===index){
        this.orderIndex=null
        return
      }
      this.orderIndex=index
    }
  }  
}
