





























import goodsDetail from '@/components/GoodsDetail.vue'
import addCart from '@/components/AddCart.vue'
export default {
name:'category',
  data(){
    return{
      goodsList:[],
      cartDetail:{},
      chooseIndex:0
    }
  },
  mounted(){
    this.$axios({
      method:'get',
      url: 'https://jiepl-show.github.io/web/data/data.json'
    }).then((response)=>{
      console.log(response)
      if(response.status==200){
        this.goodsList = response.data.data.goodsList
      }
    }).catch((error)=>{
      console.log(error)
    })
  },
  components:{
    goodsDetail,addCart
  },
  methods:{
    chooseType(index){
      this.chooseIndex=index
    },
    goodsDetail(val){
      this.$store.dispatch("set_goodsDetail",val)
      this.$store.commit("changeGoodsStatus")
    },
    cartHandle(val){
      this.cartDetail=val
      this.$store.commit("changeCartStatus")
    }
  }    
}
