









import { Dialog } from 'vant';
export default {
  name:'search',
  data(){
    return{
      searchInfo:''
    }
  },
  methods: {
    emptyInfo(){
      this.searchInfo='';
    },
    searchGoods(){
      let val = this.searchInfo;
      Dialog.alert({
        title: '搜索的商品',
        message: val,
      }).then(() => {

      });
    }
  }
}
